#portfolio {
    width: 100vw;
    max-width: 65rem;
    margin: 0 auto;
    overflow: hidden;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
}


.portTitle {
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
}

.portDesc {
    height: 100vh;
    width: 100vw;
    font-size: 1.5rem;
    font-weight: 300;
}

.card-container {
    width: 30rem;
    border-radius: 1rem;
    box-shadow: 0px 10px 8px rgb(30,30,30);
    display: flex;
    flex-direction: column;
    margin: 0.5rem;
    background-color: rgb(40,40,40);
    height: fit-content;
    transition: transform ease 300ms;
}

.card-container:hover {
        transform: translate(0, -7px);
}

.card-img {
    width: 100%;
    border-radius: 1rem 1rem 0 0;
    padding: 1rem;
}

.card-title {
    margin: 0.5rem 5%;
    font-weight:600;
    font-size:2rem;
}

.card-description {
    margin: 0.5rem 5%;
    font-size:1.25rem;
}

.card-btn {
    background: white;
    margin: 1rem 1rem;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 1.5rem;
    cursor: pointer;
}

.card-btn:hover {
    background: aqua;
}

.card-btn:visited {
        color: black;
        text-decoration: none;
    }

@media screen and (max-width:720px) {
    .card-container {
        padding-left: 10%;
        padding-right: 10%;
    }
}


